/**
 *  -0  0
 *
 * @param {number} a 
 * @param {number} b 
 * @returns {boolean} 
 */
export function is (a, b) {
  if (a !== b) {
    return false
  }

  return 1 / a === 1 / b
}

/**
 *  0  1 0  -10  0
 *
 * @param {number} a 
 * @param {number} b 
 * @returns {boolean} 
 */
export function sign (num) {
  if (num === 0) {
    return 0
  }

  return num > 0 ? 1 : -1
}

/**
 * 0 -0 
 * @param {number} num 
 * @returns {boolean} 
 */
export function isPositive (num) {
  return num > 0 || is(num, 0)
}

/**
 * 
 *
 * @param {number} a 
 * @param {number} b 
 * @param {number} [decimals=0] 
 * @returns {number}  
 */
export function add (a, b, decimals = 0) {
  return round((a + b) * Math.pow(10, decimals)) / Math.pow(10, decimals)
}

/**
 * 
 *
 * @param {number} num 
 * @param {number} decimals 
 * @returns {number}  
 */
export function round (num, decimals = 0) {
  return Number(Math.round(num + 'e' + decimals) + 'e-' + decimals)
}

/**
 *  10 
 *
 * @param {number} num 
 * @returns {number} 
 */
export function log10 (num) {
  return Math.log10 ? Math.log10(num) : Math.log(num) * Math.LOG10E
}
